@import "../../global.scss";

.intro {
  background-color: #ffffff;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imageContainer {
      width: 700px;
      height: 700px;
      background-color: crimson;
      border-radius: 50%;
      display: flex;
      justify-content: start;
      align-items: flex-end;
      float: right;
      @include mobile {
        align-items: flex-start;
      }

      img {
        height: 90%;

        @include mobile {
          width: 60%;
          height: 70%;
          position: absolute;
          right: 55px;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      justify-content: center;
      flex-direction: column;

      @include mobile {
        padding-left: 0;

        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;
        @include mobile {
          font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }
        span {
          font-size: inherit;
          color: crimson;
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 40%;
      img {
        width: 30px;
        animation: arrowBlink 2s infinite;
      }
      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
