@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;
  background-color: $mainColor;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  right: -300px;
  transition: all 1s ease;

  &.active {
    right: 0;
  }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      font-size: 30px;
      color: white;
      font-size: 30px;
      font-weight: 300;
      width: 70%;
    }
    li {
      margin-bottom: 25px;
      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }

      &:hover {
        font-weight: 500;
      }
    }
  }

